@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,900|Source+Sans+Pro:300,900&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --ff-primary: 'Source Sans Pro', sans-serif;
    --ff-secondary: 'Source Code Pro', monospace;

    --fw-reg: 300;
    --fw-bold: 900;

    --clr-light: #fff;
    --clr-dark: #303030;
    --clr-accent: #1bb6e6;
    --clr-accent-two: #040d27;
    --clr-silver: #bdbcbc;
    
    --fs-h1: 3rem;
    --fs-h2: 2.25rem;
    --fs-h3: 1.25rem;
    --fs-body: 1rem;
  
}

html {
    scroll-behavior: smooth;

    overflow-y: scroll; /* has to be scroll, not auto */
    -webkit-overflow-scrolling: touch;
}

body {
    background: #fff;
    background: var(--clr-light);
    color: #303030;
    color: var(--clr-dark);
    margin: 0;
    font-family:'Source Sans Pro', sans-serif;
    font-family:var(--ff-primary);
    font-size: 1rem;
    font-size: var(--fs-body);
    line-height: 1.6;
}


header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.nav {
    display: flex;
    justify-content: center;
    position: fixed;
    background-color: var(--clr-dark);
    background-image: url(https://media.giphy.com/media/26n6CWImwECKu3fy0/giphy.gif);
    background-size: cover;
    background-blend-mode: multiply;
    width: 100%;
    height:100%;
    color: var(--clr-light);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    transform: translateX(100%);
    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.navlinks {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    margin: 0;
    padding: 0;
}

.navlinks a {
    color: var(--clr-light);
    font-weight: var(--fw-bold);
    font-size: var(--fs-h2);
    text-decoration: none;
}

.navlinks a:hover {
    color: var(--clr-accent);
}

.nav-toggle {
    color: var(--clr-silver);
    background: transparent;
    border: none;
    font-size: 2em;
    position: absolute;
    right: .5em;
    top: .5em;
    z-index: 1000;
}

.nav-toggle:hover {
    color: var(--clr-accent);
}

.nav-open .nav{
    transform: translateX(0);
}

.nav-open .nav-toggle {
    position: fixed;
}

